<template>
  <div class="search-wrap">
    <input
      v-model="inputValue"
      class="search-input"
      :placeholder="$t('dao.search')"
    />
    <div class="search-icon" @click="handleSearch">
      <img class="icon" src="../../../assets/images/search.png">
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: ['value'],
  data () {
    return {

    }
  },
  computed: {
    inputValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
      }
    }
  },
  methods: {
    handleSearch () {
      this.$emit('handleSearch')
    }
  },
  mounted () {

  }
}
</script>

